.App {
  text-align: center;
}

.App-logo {
  max-height: 30vmin;
}

.App-header {
  background-color: #382e2c;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.runtime {
  padding-top: 2vh;
  font-size: 5vw;
  font-family: sans-serif;
}

.split {
  font-size: 1.5vw;
  height: 100%;
  width: 50%;
  position: fixed;
  z-index: 1;
  top: 0;
  overflow-x: hidden;
  padding-top: 20px;
}

.left {
  left: 0;
  background-color: #382e2c;
}

.right {
  right: 0;
  background-color: #52288e;
}
